:root {
  --jumbotron-padding-y: 3rem;
}

.jumbotron {
  padding-top: var(--jumbotron-padding-y);
  padding-bottom: var(--jumbotron-padding-y);
  margin-bottom: 0;
  background-color: #fff;
}
@media (min-width: 768px) {
  .jumbotron {
    padding-top: calc(var(--jumbotron-padding-y) * 2);
    padding-bottom: calc(var(--jumbotron-padding-y) * 2);
  }
}

@media (max-width: 768px) {
  .topsetting {
    display: block !important;
  }
  .title {
    width: 100%;
  }
  .search {
    width: 100% !important;
    margin-bottom: 10px;
  }
}
.jumbotron p:last-child {
  margin-bottom: 0;
}

.jumbotron-heading {
  font-weight: 300;
}

.jumbotron .container {
  max-width: 40rem;
}

footer {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

footer p {
  margin-bottom: 0.25rem;
}

.topsetting {
  display: flex;
  justify-content: space-between;
}
.search {
  width: 250px;
  border: 1px solid #000;
  border-radius: 5px;
  height: 40px;
  padding-left: 10px;
}
::placeholder {
  padding: 10px;
}
.mycard {
  height: 450px;
}
.info-card {
  border: 1px solid #000;
}
.fa-angle-down {
  float: right;
}
.card-img-top {
  width: 100%;
  height: 230px;
}
